.section{
  &-question{
    &__wrapper{
      position: relative;
      &::before{
        content: '';
        position: absolute;
        z-index: 2;
        height: 100%;
        width: 100%;
        left: 0;
        top: 0;
        background: url("../img/question-bckg.svg") left top no-repeat;
        background-size: contain;
      }
    }
    &__grid{
      display: grid;
      grid-template-columns: 300px auto;
      column-gap: 100px;
      height: 100%;
      min-height: 745px;
    }
    &__container{
      position: relative;
      z-index: 3;
    }
    &__info,&__content{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 100%;
    }
    &__desc{
      margin: 20px 0 15px 0;
    }
  }
}

.question{
  &-trigger{
    &:hover{
      cursor: pointer;
    }
  }
  &-list{
    display: flex;
    flex-direction: column;
    width: 100%;
    &__item + &__item{
      margin-top: 10px;
    }
  }
  &-item{
    background: #FFFFFF;
    box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    padding: 0 30px;
    &.active{
      .question-item{
        &__arrow{
          transform: rotate(180deg) translateY(-10px);
        }
        &__title{
          color:#8852FE;
        }
        &__content{
          display: block;
          height: 100%;
          visibility: unset;
          padding-bottom: 26px;
        }
      }
    }
    &__header{
      display: flex;
      align-items: center;
      justify-content: space-between;
      transition: .3s opacity;
      &:hover{
        opacity: .6;
      }
    }
    &__arrow{
      margin-left: 15px;
      width: 10px;
      height: 10px;
      flex-shrink: 0;

      transition: .5s all;
    }
    &__title{
      font-family: 'Futura PT',sans-serif;
      font-weight: 500;
      font-size: 24px;
      padding: 17px 0;

      transition: .3s color;
    }
    &__content{
      height: 0;
      visibility: hidden;
      transition: .3s all;
    }
    &__text{
      font-size: 18px;
      line-height: 1.2;
    }
  }
}