.section{
  &-plane{
    &__container{
      position: relative;
      &::before{
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        bottom: 0;
        display: block;
        background:url('../img/plain-arrow.svg') right bottom no-repeat;
        background-size: contain;
      }
    }
    &__wrapper{
      position: relative;
      overflow: hidden;
      &::before{
        content: '';
        position: absolute;
        z-index: 1;
        left: -20px;
        top: 0;
        height: 170px;
        width: 100%;
        display: block;
        background:url("../img/logo-opacity.svg") left top no-repeat;
        background-size: contain;
      }
      &::after{
        content: '';
        position: absolute;
        z-index: 2;
        right: -80px;
        bottom: 0;
        width: 100%;
        height: 320px;
        display: block;
        background: url("../img/plane.png") right bottom no-repeat;
        background-size: contain;
      }
    }
    &__content{
      max-width: 465px;
      min-height: 455px;
      padding: 75px 0 60px 0;
      display: flex;
      flex-direction: column;
      position: relative;
      z-index: 3;
    }
    &__title{
      background: linear-gradient(94.49deg, #CA3D92 0%, #FF76C9 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      text-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05);
    }
    &__btn{
      margin-top: auto;
      width: 195px;
      height: 55px;

      background: #019934;
      box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.03);
      border-radius: 10px;

      font-family: 'Futura PT', sans-serif;
      font-weight: 700;
      font-size: 20px;
    }
  }
}