.section{
  &-instagram{
    &__content{
      margin-top: 40px;
    }
  }
}

.instagram{
  &-slider{
    &__item{
      height: 360px;
    }
  }
  &-slide{
    &__content{
      height: 100%;
      width: 100%;
    }
    &__img{
      width: 100%;
      height: 100%;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}