body{
  font-family: 'Futura PT Book', sans-serif;
}

.container{
  width: 100%;
  max-width: 1170px;
  margin: 0 auto;
}

.text{
  p + p{
    margin-top: 15px;
  }
  &_accent{
    color:#019934;
  }
}

.btn{
  -webkit-appearance: none;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 100%;

  transition: .3s background-color;
  &-green{
    font-family: 'Futura PT', sans-serif;
    background-color: #019934;
    color:#fff;

    &:hover{
      background-color: rgba(#019934,.6);
    }
  }
}

.icon{
  width: 100%;
  height: 100%;
}