.section-vacancy {
  &__title {
    text-align: center;
    font-size: 200px;
    transform: translateY(100px);
    background: linear-gradient(180deg, rgba(137, 82, 255, 0.7) -3.41%, rgba(137, 82, 255, 0) 74.55%);
    background-clip: border-box;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05);
  }
  &__wrapper{
    padding-top: 0;
  }
}
.vacancy{
  &-wrapper{
    position: relative;
  }
  &-slider{
    padding: 15px;
    &__item{
      background: #FFFFFF;
      box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05);
      border-radius: 10px;

      overflow: hidden;
    }
    &-button{
      position: absolute;
      top: 50%;
      margin-top: -25px;
      &-next{
        right: 0;
        border-radius: 25px 0px 0px 25px;
      }
      &-prev{
        left: 0;
        border-radius: 0px 25px 25px 0px;
      }
    }
  }
  &-item{
    &__img{
      width: 100%;
      height: 130px;
      position: relative;
      &::before{
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        display: block;
        left: 0;
        top: 0;
        background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%);
        z-index: 1;
      }
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &__info{
      position: absolute;
      left: 10px;
      bottom: 10px;
      display: flex;
      align-items: center;
      z-index: 2;
    }
    &__country{
      display: flex;
      align-items: center;
      &-icon{
        width: 16px;
        height: 16px;
        flex-shrink: 0;
      }
      &-text{
        font-size: 14px;
        color:#fff;
        display: block;
        margin-left: 6px;
      }
    }
    &__conditions{
      display: flex;
      align-items: center;
      margin-left: 15px;
      &-icon{
        width: 16px;
        height: 16px;
      }
      &-text{
        margin-left: 5px;
        font-size: 14px;
        color:#fff;
      }
    }
    &__wrapper{
      padding: 10px 20px 20px 20px;
    }
    &__title{
      font-family: "Futura PT", sans-serif;
      font-weight: 500;
      font-size: 16px;
      min-height: 42px;
    }
    &__content{
      margin-top: 15px;
      display: flex;
      align-items: center;
    }
    &__icons{
      display: flex;
      align-items: center;
    }
    &__icon{
      display: block;
      width: 16px;
      height: 16px;
      flex-shrink: 0;
    }
    &__icon + &__icon{
      margin-left: 10px;
    }
    &__price{
      margin-left: 15px;
      &-text{
        font-size: 16px;
        color: #9C9C9C;
        span{
          font-family: 'Futura PT', sans-serif;
          font-weight: 700;
          color:#8852FE;
        }
      }
    }
    &__footer{
      margin-top: 15px;
      border-top: 1px solid #EFEFEF;
      padding-top: 15px;
    }
    &__btn{
      width: 100%;
      height: 40px;
      font-family: "Futura PT", sans-serif;
      font-size: 16px;
      font-weight: 700;
      box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.03);
      border-radius: 10px;
    }
    &__link{
      display: flex;
      align-items: center;
      text-align: center;
      justify-content: center;

      margin-top: 10px;
      font-size: 16px;
      color:#9C9C9C;
      text-decoration: underline;
      text-underline-position: under;

      transition: 0.3s background-color;
      &:hover{
        color:rgba(#9C9C9C,.6);
      }
    }
  }
}