.section{
  &-articles{
    &__wrapper{
      background: rgba(136, 82, 254, 0.05);
      padding: 70px 0 90px 0;
    }
    &__header{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__content{
      margin-top: 30px;
    }
  }
}

.articles{
  &-grid{
    display: grid;
    grid-template-columns: repeat(4,minmax(0,1fr));
    grid-auto-rows: 285px;
    gap: 10px;
    &__item{
      position: relative;
      &_big{
        grid-column:3 / span 2;
        grid-row:1 / span 2;
      }
    }
  }
}

.article{
  &-item{
    height: 100%;
    width: 100%;
    &:hover{
      .article{
        &-item{
          &__img{
            img{
              transform: scale(1.1);
            }
          }
          &__wrapper{
            opacity: .8;
          }
        }
      }
    }
    &__img{
      position: absolute;
      z-index: 1;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;

      overflow: hidden;
      &::before{
        content: '';
        position: absolute;
        z-index: 2;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        background: rgba(0, 0, 0, 0.5);

      }
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;

        transition: .3s all linear;

      }
    }
    &__wrapper{
      position: relative;
      z-index: 3;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 20px;
      height: 100%;
      width: 100%;
      transition: .3s opacity;
    }
    &__content{
      margin-top: auto;
    }
    &__header{
      display: flex;
      align-items: center;
      justify-content: flex-end;
    }
    &__icon{
      width: 30px;
      height: 30px;
    }
    &__title{
      font-family: 'Futura PT', sans-serif;
      font-weight: 500;
      font-size: 22px;
      line-height: 1.2;
      color:#fff;
      &_big{
        font-size: 36px;
      }
    }
    &__footer{
      display: flex;
      align-items: center;

      margin-top: 13px;
    }
    &__date{
      font-size: 18px;
      color:#fff;
    }
    &__comment{
      margin-left: 15px;
      display: flex;
      align-items: center;
      &-icon{
        width: 15px;
        height: 15px;
      }
      &-count{
        margin-left: 6px;
        color:#fff;
        font-size: 18px;
      }
    }
  }
}