.section{
  &-stats{
    &__link{
      margin: 20px auto 0 auto;
      text-align: center;
      display: block;
    }
    &__slider{
      margin-top: 45px;
    }
    &__sub-desc{
      text-align: center;
    }
    &__btn{
      width: 350px;
      height: 56px;
      margin: 20px auto 0 auto;

      box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.03);
      border-radius: 10px;

      font-size: 20px;
    }
  }
}

.stats{
  &-wrapper{
    &__slider{
      position: relative;
    }
    &__footer{
      margin-top: 50px;
    }
  }
  &-footer{
    padding-top: 40px;
    border-top: 1px solid #EFEFEF;
  }
  &-item{
    &__wrapper{
      padding: 20px 30px 20px 20px;
      background: rgba(136, 82, 254, 0.05);
      border-radius: 15px;
    }
    &__chart{
      width: 100%;
      height: 150px;
      background: #FFFFFF;
      border-radius: 10px;
    }
    &__content{
      padding-top: 20px;
    }
    &__title{
      font-family: 'Futura PT', sans-serif;
      font-weight: 500;
      font-size: 24px;
      line-height: 1.2;
    }
    &__list{
      margin-top: 24px;
    }
  }
  &-slider{
    &-button{
      position: absolute;
      top: 50%;
      margin-top: -25px;
      &-next{
        right: 0;
        border-radius: 25px 0px 0px 25px;
      }
      &-prev{
        left: 0;
        border-radius: 0px 25px 25px 0px;
      }
    }
  }
}

.chart{
  &-wrapper{
    display: flex;
    align-items: flex-end;
    justify-content: center;

    height: 100%;
    &__item + &__item{
      margin-left: 30px;
    }
    &__item{
      display: block;
    }
  }
  &__item{
    width: 20px;
    border-radius: 15px 15px 0px 0px;
    &_orange{
      background-color: #F2994A;
    }
    &_green{
      background-color: #019934;
    }
    &_blue{
      background-color: #8952FF;
    }
    &_purple{
      background-color: #CA3D92;
    }
  }
}

.legend{
  &-list{
    &__item + &__item{
      margin-top: 10px;
    }
  }
  &-item{
    display: flex;
    align-items: center;
    &__icon{
      width: 10px;
      height: 10px;
      border-radius: 50%;
      display: block;
      margin-right: 12px;
      &_orange{
        background-color: #F2994A;
      }
      &_green{
        background-color: #019934;
      }
      &_blue{
        background-color: #8952FF;
      }
      &_purple{
        background-color: #CA3D92;
      }
    }
    &__text{
      font-size: 18px;
    }
  }
}