.slider{
  &-control{
    &-button{
      width: 50px;
      height: 50px;
      background-color: #8852FE;

      display: flex;
      align-items: center;
      justify-content: center;

      transition: .3s opacity;
      svg{
        width: 10px;
        height: 10px;
      }
      &:hover{
        cursor: pointer;
        opacity: .8;
      }
      &.swiper-button-disabled{
        pointer-events: none;
        opacity: .3;
      }
    }
  }
}