.section{
  &-country{
    &__desc{
      margin: 15px auto 0 auto;
      text-align: center;
    }
    &__content{
      margin-top: 52px;
    }
    &__slider{
      position: relative;
    }
  }
}

.country{
  &-slider{
    &-button{
      position: absolute;
      top: 50%;
      margin-top: -25px;
      &-next{
        right: 0;
        border-radius: 25px 0px 0px 25px;
      }
      &-prev{
        left: 0;
        border-radius: 0px 25px 25px 0px;
      }
    }
  }
  &-slide{
    &__wrapper{
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    &__img{
      width: 170px;
      height: 170px;
      border-radius: 50%;
      background-color: #Fff;
      box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05);

      display: flex;
      align-items: center;
      justify-content: center;
      img{
        width: 136px;
        height: 136px;
        border-radius: 50%;
        filter: drop-shadow(0px 4px 30px rgba(0, 0, 0, 0.05));
        object-fit: cover;
      }
    }
    &__content{
      margin-top: 15px;
    }
    &__title{
      font-family: "Futura PT", sans-serif;
      font-weight: 500;
      line-height: 1.2;
      font-size: 24px;
      text-align: center;
    }
    &__count{
      font-size: 18px;
      line-height: 1.2;
      color: #6948E7;
      text-align: center;
    }
    &__footer{
      margin-top: 16px;
      width: 100%;
    }
    &__btn{
      font-family: 'Futura PT',sans-serif;
      font-weight: 700;
      color:#fff;
      font-size: 16px;
      max-width: 230px;
      height: 40px;
      margin: 0 auto;

      box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.03);
      border-radius: 10px;
    }
  }
}