.section{
  &-living{
    background: rgba(136, 82, 254, 0.05);
    position: relative;
    margin-top: 130px;
    &::before{
      content: '';
      position: absolute;
      width: 100%;
      height: 640px;
      right: 0;
      top: -60px;
      background:url("../img/living-arrow.svg") right top no-repeat;
      background-size: contain;
    }
    &__wrapper{
      padding: 65px 0 85px 0;
    }
    &__info{
      max-width: 660px;
      width: 100%;
      position: relative;
    }
    &__content{
      margin-top: 55px;
    }
    &__link{
      margin-top: 22px;
      display: block;
    }
    &__title{
      background: linear-gradient(94.49deg, #CA3D92 0%, #FF76C9 100%);
      background-clip: border-box;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      text-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05);
    }
    &__container{
      position: relative;
    }
    &__controls{
      position: absolute;
      right: 0;
      bottom: -25px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 25px;
      overflow: hidden;
    }
  }
}

.living{
  &-slider{
    &__item{
      width: 270px;
      height: 270px;
    }
  }
  &-item{
    width: 100%;
    height: 100%;
    &__img{
      width: 100%;
      height: 100%;
    }
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}