.section{
  &__wrapper{
    padding: 60px 0;
  }
  &__title{
    font-family: 'Futura PT', sans-serif;
    font-size: 42px;
    line-height: 1.1;
    font-weight: 700;
    &_center{
      text-align: center;
    }
    &-purple{
      color:#8952FF;
    }
  }
  &__desc{
    margin-top: 30px;
    font-size: 18px;
    line-height: 1.2;
    &_center{
      max-width: 526px;
      margin: 15px auto 0 auto;
      text-align: center;
    }
  }
  &__sub-desc{
    font-size: 18px;
    color:#6948E7;
  }
  &__link{
    font-size: 16px;
    color:#9C9C9C;
    text-decoration: underline;
    text-underline-position: under;

    transition: 0.3s color;
    &:hover{
      color: rgba(#9C9C9C,.6)
    }
  }
}