.section {
  &-about {
    &__content {
      display: grid;
      grid-template-columns: repeat(2, minmax(0, 1fr));
      column-gap: 70px;
    }

    &__link {
      margin-top: 25px;
      display: block;
    }

    &__grid {
      display: grid;
      grid-template-columns: repeat(3, minmax(0, 1fr));
      column-gap: 30px;

      margin-top: 70px;
    }
  }
}

.about {
  &-grid {
    counter-reset: about;

    &__item {
      padding: 20px 20px 25px 20px;

      box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05);
      border-radius: 10px;

      counter-increment: about;
    }
  }

  &-item {
    &__header {
      display: flex;
      align-items: center;

      &::before {
        content: counter(about);
        flex-shrink: 0;
        text-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05);

        font-size: 42px;
        font-family: 'Futura PT';
        font-weight: 700;
        color:#CA3D92;
      }
    }
    &__title{
      margin-left: 25px;
      font-family: 'Futura PT';
      font-weight: 500;
      font-size: 24px;
      line-height: 1.2;

    }
    &__content{
      margin-top: 20px;
    }
    &__desc{
      font-size: 18px;
      line-height: 1.2;
    }
  }
}