*,
*::after,
*::before {
  box-sizing: border-box;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

body {
  font-family: 'HelveticaNeue';
  font-weight: 400;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
  height: 100%;
}

ol,
ul {
  padding: 0;
  list-style: none;
}

a {
  font-family: inherit;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  border: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
}

@font-face {
  font-family: 'Futura PT';
  src: url("../fonts/FuturaPT-Medium/FuturaPT-Medium.woff2") format("woff2"), url("../fonts/FuturaPT-Medium/FuturaPT-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Futura PT';
  src: url("../fonts/FuturaPT-Bold/FuturaPT-Bold.woff2") format("woff2"), url("../fonts/FuturaPT-Bold/FuturaPT-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Futura PT Book';
  src: url("../fonts/FuturaPT-Book/FuturaPT-Book.woff2") format("woff2"), url("../fonts/FuturaPT-Book/FuturaPT-Book.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

body {
  font-family: 'Futura PT Book', sans-serif;
}

.container {
  width: 100%;
  max-width: 1170px;
  margin: 0 auto;
}

.text p + p {
  margin-top: 15px;
}

.text_accent {
  color: #019934;
}

.btn {
  -webkit-appearance: none;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  transition: .3s background-color;
}

.btn-green {
  font-family: 'Futura PT', sans-serif;
  background-color: #019934;
  color: #fff;
}

.btn-green:hover {
  background-color: rgba(1, 153, 52, 0.6);
}

.icon {
  width: 100%;
  height: 100%;
}

.header__wrapper, .footer__wrapper {
  display: flex;
  align-items: center;
}

.header__btn, .footer__btn {
  margin-left: 45px;
}

.header-logo__wrapper, .footer-logo__wrapper {
  display: flex;
  align-items: center;
}

.header-logo__content, .footer-logo__content {
  display: flex;
  align-items: center;
}

.header-logo__img, .footer-logo__img {
  width: 200px;
  height: 40px;
}

.header-logo__img-item, .footer-logo__img-item {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.header-logo__content, .footer-logo__content {
  margin-left: 25px;
  border-left: 1px solid #EFEFEF;
  padding-left: 25px;
}

.header-logo__country, .footer-logo__country {
  width: 50px;
  height: 35px;
  flex-shrink: 0;
}

.header-logo__country-item, .footer-logo__country-item {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.header-logo__text, .footer-logo__text {
  font-size: 18px;
  color: #BDBDBD;
  text-transform: uppercase;
  margin-left: 25px;
}

.header__entry, .footer__entry {
  font-size: 18px;
  font-family: 'Futura PT', sans-serif;
  font-weight: 500;
  color: #019934;
  transition: 0.3s color;
}

.header__entry:hover, .footer__entry:hover {
  color: rgba(1, 153, 52, 0.6);
}

.header__wrapper {
  padding-top: 40px;
}

.header__nav {
  margin-left: auto;
}

.nav-list__item + .nav-list__item {
  margin-left: 45px;
}

.nav-item__link {
  font-family: 'Futura PT', sans-serif;
  font-weight: 500;
  color: #BDBDBD;
  font-size: 18px;
  transition: .3s color;
}

.nav-item__link:hover {
  color: rgba(189, 189, 189, 0.6);
}

.nav__list {
  display: flex;
  align-items: center;
}

.footer {
  margin-top: 70px;
}

.footer__row {
  padding: 30px 0;
}

.footer__row + .footer__row {
  border-top: 1px solid #EFEFEF;
}

.footer__btn {
  margin-right: auto;
}

.footer__copyright {
  color: #6948E7;
  font-size: 18px;
}

.footer__email {
  color: #6948E7;
  font-size: 18px;
}

.footer__block_copyright {
  margin-right: auto;
  flex-shrink: 0;
}

.footer__block_social {
  margin-left: 30px;
}

.footer-social {
  display: flex;
  align-items: center;
}

.footer-social__item + .footer-social__item {
  margin-left: 15px;
}

.footer-social__icon {
  display: block;
  height: 17px;
  max-width: 24px;
}

.section__wrapper {
  padding: 60px 0;
}

.section__title {
  font-family: 'Futura PT', sans-serif;
  font-size: 42px;
  line-height: 1.1;
  font-weight: 700;
}

.section__title_center {
  text-align: center;
}

.section__title-purple {
  color: #8952FF;
}

.section__desc {
  margin-top: 30px;
  font-size: 18px;
  line-height: 1.2;
}

.section__desc_center {
  max-width: 526px;
  margin: 15px auto 0 auto;
  text-align: center;
}

.section__sub-desc {
  font-size: 18px;
  color: #6948E7;
}

.section__link {
  font-size: 16px;
  color: #9C9C9C;
  text-decoration: underline;
  text-underline-position: under;
  transition: 0.3s color;
}

.section__link:hover {
  color: rgba(156, 156, 156, 0.6);
}

.section-hero {
  position: relative;
}

.section-hero::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 745px;
  display: block;
  right: 0;
  bottom: 0;
  background: url("../img/map-big.svg") right top no-repeat;
  background-size: contain;
}

.section-hero__wrapper {
  padding-top: 30px;
  position: relative;
  z-index: 3;
}

.section-hero__grid {
  display: grid;
  grid-template-columns: 570px 530px;
  column-gap: 50px;
}

.section-hero__item {
  min-height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.section-hero__title {
  max-width: 475px;
}

.section-hero__footer {
  margin-top: 50px;
  border-top: 1px solid #EFEFEF;
  padding-top: 50px;
}

.section-hero__btn {
  font-family: 'Futura PT', sans-serif;
  font-weight: 700;
  font-size: 20px;
  margin-top: 20px;
  width: 200px;
  height: 56px;
  box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.03);
  border-radius: 10px;
}

.person-slider {
  position: relative;
}

.person-slider-button-next {
  width: 5px;
  height: 10px;
  display: flex;
  flex-shrink: 0;
  margin-left: 20px;
  transition: 0.3s opacity;
}

.person-slider-button-next:hover {
  cursor: pointer;
  opacity: .6;
}

.person-item {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.person-item__content {
  background: #FFFFFF;
  box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
  padding: 15px 20px;
}

.person-item__header {
  display: flex;
  align-items: center;
}

.person-item__name {
  font-family: 'Futura PT',sans-serif;
  font-weight: 500;
  font-size: 24px;
}

.person-item__social {
  margin-left: 20px;
  display: flex;
  align-items: center;
}

.person-item__social-link + .person-item__social-link {
  margin-left: 15px;
}

.person-item__social-link {
  font-size: 16px;
  color: #019934;
  text-decoration: underline;
  text-underline-position: under;
}

.person-item__social-link_insta {
  color: #6948e7;
}

.person-item__footer {
  margin-top: 10px;
}

.person-item__desc {
  color: rgba(40, 40, 40, 0.5);
  font-size: 16px;
}

.person-item__img {
  width: 100%;
  max-width: 505px;
  max-height: 586px;
  height: 100%;
}

.person-item__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero-slider__controls {
  position: absolute;
  right: 25px;
  bottom: 60px;
  display: flex;
  align-items: center;
  z-index: 2;
}

.hero-slider__controls .swiper-pagination {
  position: relative;
  bottom: unset;
  left: unset;
}

.hero-slider__controls .swiper-pagination-bullet {
  background-color: #E5E5E5;
  width: 10px;
  height: 10px;
  opacity: 1;
  transition: .3s opacity;
}

.hero-slider__controls .swiper-pagination-bullet:hover {
  opacity: .6;
}

.hero-slider__controls .swiper-pagination-bullet-active {
  background: linear-gradient(131.82deg, #5D44DE 0%, #8952FF 96.22%);
}

.section-about__content {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  column-gap: 70px;
}

.section-about__link {
  margin-top: 25px;
  display: block;
}

.section-about__grid {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  column-gap: 30px;
  margin-top: 70px;
}

.about-grid {
  counter-reset: about;
}

.about-grid__item {
  padding: 20px 20px 25px 20px;
  box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
  counter-increment: about;
}

.about-item__header {
  display: flex;
  align-items: center;
}

.about-item__header::before {
  content: counter(about);
  flex-shrink: 0;
  text-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05);
  font-size: 42px;
  font-family: 'Futura PT';
  font-weight: 700;
  color: #CA3D92;
}

.about-item__title {
  margin-left: 25px;
  font-family: 'Futura PT';
  font-weight: 500;
  font-size: 24px;
  line-height: 1.2;
}

.about-item__content {
  margin-top: 20px;
}

.about-item__desc {
  font-size: 18px;
  line-height: 1.2;
}

.section-vacancy__title {
  text-align: center;
  font-size: 200px;
  transform: translateY(100px);
  background: linear-gradient(180deg, rgba(137, 82, 255, 0.7) -3.41%, rgba(137, 82, 255, 0) 74.55%);
  background-clip: border-box;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05);
}

.section-vacancy__wrapper {
  padding-top: 0;
}

.vacancy-wrapper {
  position: relative;
}

.vacancy-slider {
  padding: 15px;
}

.vacancy-slider__item {
  background: #FFFFFF;
  box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
  overflow: hidden;
}

.vacancy-slider-button {
  position: absolute;
  top: 50%;
  margin-top: -25px;
}

.vacancy-slider-button-next {
  right: 0;
  border-radius: 25px 0px 0px 25px;
}

.vacancy-slider-button-prev {
  left: 0;
  border-radius: 0px 25px 25px 0px;
}

.vacancy-item__img {
  width: 100%;
  height: 130px;
  position: relative;
}

.vacancy-item__img::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  display: block;
  left: 0;
  top: 0;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%);
  z-index: 1;
}

.vacancy-item__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.vacancy-item__info {
  position: absolute;
  left: 10px;
  bottom: 10px;
  display: flex;
  align-items: center;
  z-index: 2;
}

.vacancy-item__country {
  display: flex;
  align-items: center;
}

.vacancy-item__country-icon {
  width: 16px;
  height: 16px;
  flex-shrink: 0;
}

.vacancy-item__country-text {
  font-size: 14px;
  color: #fff;
  display: block;
  margin-left: 6px;
}

.vacancy-item__conditions {
  display: flex;
  align-items: center;
  margin-left: 15px;
}

.vacancy-item__conditions-icon {
  width: 16px;
  height: 16px;
}

.vacancy-item__conditions-text {
  margin-left: 5px;
  font-size: 14px;
  color: #fff;
}

.vacancy-item__wrapper {
  padding: 10px 20px 20px 20px;
}

.vacancy-item__title {
  font-family: "Futura PT", sans-serif;
  font-weight: 500;
  font-size: 16px;
  min-height: 42px;
}

.vacancy-item__content {
  margin-top: 15px;
  display: flex;
  align-items: center;
}

.vacancy-item__icons {
  display: flex;
  align-items: center;
}

.vacancy-item__icon {
  display: block;
  width: 16px;
  height: 16px;
  flex-shrink: 0;
}

.vacancy-item__icon + .vacancy-item__icon {
  margin-left: 10px;
}

.vacancy-item__price {
  margin-left: 15px;
}

.vacancy-item__price-text {
  font-size: 16px;
  color: #9C9C9C;
}

.vacancy-item__price-text span {
  font-family: 'Futura PT', sans-serif;
  font-weight: 700;
  color: #8852FE;
}

.vacancy-item__footer {
  margin-top: 15px;
  border-top: 1px solid #EFEFEF;
  padding-top: 15px;
}

.vacancy-item__btn {
  width: 100%;
  height: 40px;
  font-family: "Futura PT", sans-serif;
  font-size: 16px;
  font-weight: 700;
  box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.03);
  border-radius: 10px;
}

.vacancy-item__link {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  margin-top: 10px;
  font-size: 16px;
  color: #9C9C9C;
  text-decoration: underline;
  text-underline-position: under;
  transition: 0.3s background-color;
}

.vacancy-item__link:hover {
  color: rgba(156, 156, 156, 0.6);
}

.section-living {
  background: rgba(136, 82, 254, 0.05);
  position: relative;
  margin-top: 130px;
}

.section-living::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 640px;
  right: 0;
  top: -60px;
  background: url("../img/living-arrow.svg") right top no-repeat;
  background-size: contain;
}

.section-living__wrapper {
  padding: 65px 0 85px 0;
}

.section-living__info {
  max-width: 660px;
  width: 100%;
  position: relative;
}

.section-living__content {
  margin-top: 55px;
}

.section-living__link {
  margin-top: 22px;
  display: block;
}

.section-living__title {
  background: linear-gradient(94.49deg, #CA3D92 0%, #FF76C9 100%);
  background-clip: border-box;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05);
}

.section-living__container {
  position: relative;
}

.section-living__controls {
  position: absolute;
  right: 0;
  bottom: -25px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 25px;
  overflow: hidden;
}

.living-slider__item {
  width: 270px;
  height: 270px;
}

.living-item {
  width: 100%;
  height: 100%;
}

.living-item__img {
  width: 100%;
  height: 100%;
}

.living-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.section-map__wrapper {
  height: 800px;
  padding: 0;
}

.map {
  height: 100%;
  width: 100%;
}

.map img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.map-container {
  position: relative;
  width: 100%;
  height: 100%;
}

.map-container__content {
  height: 100%;
  width: 100%;
}

.map-container__info {
  width: 100%;
  position: absolute;
  left: 0;
  background-color: transparent;
  bottom: 40px;
}

.map-info {
  background: #FFFFFF;
  box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
  max-width: 375px;
  padding: 0 20px;
}

.map-info__item {
  padding: 20px 0 25px 0;
}

.map-info__item + .map-info__item {
  border-top: 1px solid #EAEAEA;
}

.map-info__title {
  font-family: 'Futura PT', sans-serif;
  font-weight: 500;
  font-size: 24px;
  line-height: 1.2;
}

.map-info__desc {
  font-size: 18px;
  line-height: 1.2;
  margin-top: 15px;
}

.map-list__item {
  display: flex;
  align-items: center;
}

.map-list__item + .map-list__item {
  margin-top: 10px;
}

.map-list__icon {
  display: block;
  width: 30px;
  height: 30px;
  margin-right: 15px;
}

.section-stats__link {
  margin: 20px auto 0 auto;
  text-align: center;
  display: block;
}

.section-stats__slider {
  margin-top: 45px;
}

.section-stats__sub-desc {
  text-align: center;
}

.section-stats__btn {
  width: 350px;
  height: 56px;
  margin: 20px auto 0 auto;
  box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.03);
  border-radius: 10px;
  font-size: 20px;
}

.stats-wrapper__slider {
  position: relative;
}

.stats-wrapper__footer {
  margin-top: 50px;
}

.stats-footer {
  padding-top: 40px;
  border-top: 1px solid #EFEFEF;
}

.stats-item__wrapper {
  padding: 20px 30px 20px 20px;
  background: rgba(136, 82, 254, 0.05);
  border-radius: 15px;
}

.stats-item__chart {
  width: 100%;
  height: 150px;
  background: #FFFFFF;
  border-radius: 10px;
}

.stats-item__content {
  padding-top: 20px;
}

.stats-item__title {
  font-family: 'Futura PT', sans-serif;
  font-weight: 500;
  font-size: 24px;
  line-height: 1.2;
}

.stats-item__list {
  margin-top: 24px;
}

.stats-slider-button {
  position: absolute;
  top: 50%;
  margin-top: -25px;
}

.stats-slider-button-next {
  right: 0;
  border-radius: 25px 0px 0px 25px;
}

.stats-slider-button-prev {
  left: 0;
  border-radius: 0px 25px 25px 0px;
}

.chart-wrapper {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  height: 100%;
}

.chart-wrapper__item + .chart-wrapper__item {
  margin-left: 30px;
}

.chart-wrapper__item {
  display: block;
}

.chart__item {
  width: 20px;
  border-radius: 15px 15px 0px 0px;
}

.chart__item_orange {
  background-color: #F2994A;
}

.chart__item_green {
  background-color: #019934;
}

.chart__item_blue {
  background-color: #8952FF;
}

.chart__item_purple {
  background-color: #CA3D92;
}

.legend-list__item + .legend-list__item {
  margin-top: 10px;
}

.legend-item {
  display: flex;
  align-items: center;
}

.legend-item__icon {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  display: block;
  margin-right: 12px;
}

.legend-item__icon_orange {
  background-color: #F2994A;
}

.legend-item__icon_green {
  background-color: #019934;
}

.legend-item__icon_blue {
  background-color: #8952FF;
}

.legend-item__icon_purple {
  background-color: #CA3D92;
}

.legend-item__text {
  font-size: 18px;
}

.section-articles__wrapper {
  background: rgba(136, 82, 254, 0.05);
  padding: 70px 0 90px 0;
}

.section-articles__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.section-articles__content {
  margin-top: 30px;
}

.articles-grid {
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  grid-auto-rows: 285px;
  gap: 10px;
}

.articles-grid__item {
  position: relative;
}

.articles-grid__item_big {
  grid-column: 3 / span 2;
  grid-row: 1 / span 2;
}

.article-item {
  height: 100%;
  width: 100%;
}

.article-item:hover .article-item__img img {
  transform: scale(1.1);
}

.article-item:hover .article-item__wrapper {
  opacity: .8;
}

.article-item__img {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  overflow: hidden;
}

.article-item__img::before {
  content: '';
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.5);
}

.article-item__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: .3s all linear;
}

.article-item__wrapper {
  position: relative;
  z-index: 3;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px;
  height: 100%;
  width: 100%;
  transition: .3s opacity;
}

.article-item__content {
  margin-top: auto;
}

.article-item__header {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.article-item__icon {
  width: 30px;
  height: 30px;
}

.article-item__title {
  font-family: 'Futura PT', sans-serif;
  font-weight: 500;
  font-size: 22px;
  line-height: 1.2;
  color: #fff;
}

.article-item__title_big {
  font-size: 36px;
}

.article-item__footer {
  display: flex;
  align-items: center;
  margin-top: 13px;
}

.article-item__date {
  font-size: 18px;
  color: #fff;
}

.article-item__comment {
  margin-left: 15px;
  display: flex;
  align-items: center;
}

.article-item__comment-icon {
  width: 15px;
  height: 15px;
}

.article-item__comment-count {
  margin-left: 6px;
  color: #fff;
  font-size: 18px;
}

.section-question__wrapper {
  position: relative;
}

.section-question__wrapper::before {
  content: '';
  position: absolute;
  z-index: 2;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  background: url("../img/question-bckg.svg") left top no-repeat;
  background-size: contain;
}

.section-question__grid {
  display: grid;
  grid-template-columns: 300px auto;
  column-gap: 100px;
  height: 100%;
  min-height: 745px;
}

.section-question__container {
  position: relative;
  z-index: 3;
}

.section-question__info, .section-question__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.section-question__desc {
  margin: 20px 0 15px 0;
}

.question-trigger:hover {
  cursor: pointer;
}

.question-list {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.question-list__item + .question-list__item {
  margin-top: 10px;
}

.question-item {
  background: #FFFFFF;
  box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
  padding: 0 30px;
}

.question-item.active .question-item__arrow {
  transform: rotate(180deg) translateY(-10px);
}

.question-item.active .question-item__title {
  color: #8852FE;
}

.question-item.active .question-item__content {
  display: block;
  height: 100%;
  visibility: unset;
  padding-bottom: 26px;
}

.question-item__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: .3s opacity;
}

.question-item__header:hover {
  opacity: .6;
}

.question-item__arrow {
  margin-left: 15px;
  width: 10px;
  height: 10px;
  flex-shrink: 0;
  transition: .5s all;
}

.question-item__title {
  font-family: 'Futura PT',sans-serif;
  font-weight: 500;
  font-size: 24px;
  padding: 17px 0;
  transition: .3s color;
}

.question-item__content {
  height: 0;
  visibility: hidden;
  transition: .3s all;
}

.question-item__text {
  font-size: 18px;
  line-height: 1.2;
}

.section-instagram__content {
  margin-top: 40px;
}

.instagram-slider__item {
  height: 360px;
}

.instagram-slide__content {
  height: 100%;
  width: 100%;
}

.instagram-slide__img {
  width: 100%;
  height: 100%;
}

.instagram-slide__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.section-country__desc {
  margin: 15px auto 0 auto;
  text-align: center;
}

.section-country__content {
  margin-top: 52px;
}

.section-country__slider {
  position: relative;
}

.country-slider-button {
  position: absolute;
  top: 50%;
  margin-top: -25px;
}

.country-slider-button-next {
  right: 0;
  border-radius: 25px 0px 0px 25px;
}

.country-slider-button-prev {
  left: 0;
  border-radius: 0px 25px 25px 0px;
}

.country-slide__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.country-slide__img {
  width: 170px;
  height: 170px;
  border-radius: 50%;
  background-color: #Fff;
  box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05);
  display: flex;
  align-items: center;
  justify-content: center;
}

.country-slide__img img {
  width: 136px;
  height: 136px;
  border-radius: 50%;
  filter: drop-shadow(0px 4px 30px rgba(0, 0, 0, 0.05));
  object-fit: cover;
}

.country-slide__content {
  margin-top: 15px;
}

.country-slide__title {
  font-family: "Futura PT", sans-serif;
  font-weight: 500;
  line-height: 1.2;
  font-size: 24px;
  text-align: center;
}

.country-slide__count {
  font-size: 18px;
  line-height: 1.2;
  color: #6948E7;
  text-align: center;
}

.country-slide__footer {
  margin-top: 16px;
  width: 100%;
}

.country-slide__btn {
  font-family: 'Futura PT',sans-serif;
  font-weight: 700;
  color: #fff;
  font-size: 16px;
  max-width: 230px;
  height: 40px;
  margin: 0 auto;
  box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.03);
  border-radius: 10px;
}

.section-plane__container {
  position: relative;
}

.section-plane__container::before {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
  display: block;
  background: url("../img/plain-arrow.svg") right bottom no-repeat;
  background-size: contain;
}

.section-plane__wrapper {
  position: relative;
  overflow: hidden;
}

.section-plane__wrapper::before {
  content: '';
  position: absolute;
  z-index: 1;
  left: -20px;
  top: 0;
  height: 170px;
  width: 100%;
  display: block;
  background: url("../img/logo-opacity.svg") left top no-repeat;
  background-size: contain;
}

.section-plane__wrapper::after {
  content: '';
  position: absolute;
  z-index: 2;
  right: -80px;
  bottom: 0;
  width: 100%;
  height: 320px;
  display: block;
  background: url("../img/plane.png") right bottom no-repeat;
  background-size: contain;
}

.section-plane__content {
  max-width: 465px;
  min-height: 455px;
  padding: 75px 0 60px 0;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 3;
}

.section-plane__title {
  background: linear-gradient(94.49deg, #CA3D92 0%, #FF76C9 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05);
}

.section-plane__btn {
  margin-top: auto;
  width: 195px;
  height: 55px;
  background: #019934;
  box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.03);
  border-radius: 10px;
  font-family: 'Futura PT', sans-serif;
  font-weight: 700;
  font-size: 20px;
}

.slider-control-button {
  width: 50px;
  height: 50px;
  background-color: #8852FE;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: .3s opacity;
}

.slider-control-button svg {
  width: 10px;
  height: 10px;
}

.slider-control-button:hover {
  cursor: pointer;
  opacity: .8;
}

.slider-control-button.swiper-button-disabled {
  pointer-events: none;
  opacity: .3;
}
