.section{
  &-map{
    &__wrapper{
      height: 800px;
      padding: 0;
    }
  }
}

.map{
  height: 100%;
  width: 100%;
  img{
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &-container{
    position: relative;
    width: 100%;
    height: 100%;
    &__content{
      height: 100%;
      width: 100%;
    }
    &__info{
      width: 100%;
      position: absolute;
      left: 0;
      background-color: transparent;
      bottom: 40px;
    }
  }
  &-info{
    background: #FFFFFF;
    box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    max-width: 375px;
    padding: 0 20px;
    &__item{
      padding: 20px 0 25px 0;
    }
    &__item + &__item{
      border-top: 1px solid #EAEAEA;
    }
    &__title{
      font-family: 'Futura PT', sans-serif;
      font-weight: 500;
      font-size: 24px;
      line-height: 1.2;
    }
    &__desc{
      font-size: 18px;
      line-height: 1.2;

      margin-top: 15px;
    }
  }
  &-list{
    &__item{
      display: flex;
      align-items: center;
    }
    &__item + &__item{
      margin-top: 10px;
    }
    &__icon{
      display: block;
      width: 30px;
      height: 30px;

      margin-right: 15px;
    }

  }
}