.footer{
  margin-top: 70px;
  &__row{
    padding: 30px 0;
  }
  &__row + &__row{
    border-top: 1px solid #EFEFEF;
  }
  &__btn{
    margin-right: auto;
  }
  &__copyright{
    color:#6948E7;
    font-size: 18px;
  }
  &__email{
    color:#6948E7;
    font-size: 18px;
  }
  &__block{
    &_copyright{
      margin-right: auto;
      flex-shrink: 0;
    }
    &_social{
      margin-left: 30px;
    }
  }
  &-social{
    display: flex;
    align-items: center;
    &__item + &__item{
      margin-left: 15px;
    }
    &__icon{
      display: block;
      height: 17px;
      max-width: 24px;
    }
  }
}
