.header,.footer{
  &__wrapper{
    display: flex;
    align-items: center;
  }
  &__btn{
    margin-left: 45px;
  }
  &-logo{
    &__wrapper{
      display: flex;
      align-items: center;
    }
    &__content{
      display: flex;
      align-items: center;
    }
    &__img{
      width: 200px;
      height: 40px;
      &-item{
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
    &__content{
      margin-left: 25px;
      border-left: 1px solid #EFEFEF;
      padding-left: 25px;
    }
    &__country{
      width: 50px;
      height: 35px;
      flex-shrink: 0;
      &-item{
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
    &__text{
      font-size: 18px;
      color:#BDBDBD;
      text-transform: uppercase;
      margin-left: 25px;
    }
  }
  &__entry{
    font-size: 18px;
    font-family: 'Futura PT', sans-serif;
    font-weight: 500;
    color:#019934;

    transition: 0.3s color;
    &:hover{
      color:rgba(#019934,.6);
    }
  }
}

.header{
  &__wrapper{
    padding-top: 40px;
  }
  &__nav{
    margin-left: auto;
  }
}

.nav{
  &-list{
    &__item + &__item{
      margin-left: 45px;
    }
  }
  &-item{
    &__link{
      font-family: 'Futura PT', sans-serif;
      font-weight: 500;
      color:#BDBDBD;
      font-size: 18px;

      transition: .3s color;
      &:hover{
        color:rgba(#BDBDBD,.6)
      }
    }
  }
  &__list{
    display: flex;
    align-items: center;
  }
}