.section{
  &-hero{
    position: relative;
    &::before{
      content: '';
      position: absolute;
      width: 100%;
      height: 745px;
      display: block;
      right: 0;
      bottom: 0;
      background: url("../img/map-big.svg") right top no-repeat;
      background-size: contain;
    }
    &__wrapper{
      padding-top: 30px;
      position: relative;
      z-index: 3;
    }
    &__grid{
      display: grid;
      grid-template-columns: 570px 530px;
      column-gap: 50px;
    }
    &__item{
      min-height: 500px;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
    &__title{
      max-width: 475px;
    }
    &__footer{
      margin-top: 50px;
      border-top: 1px solid #EFEFEF;
      padding-top: 50px;
    }
    &__btn{
      font-family: 'Futura PT', sans-serif;
      font-weight: 700;
      font-size: 20px;
      margin-top: 20px;
      width: 200px;
      height: 56px;
      box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.03);
      border-radius: 10px;
    }
  }
}

.person{
  &-slider{
    position: relative;
    &-button{
      &-next{
        width: 5px;
        height: 10px;
        display: flex;
        flex-shrink: 0;
        margin-left: 20px;

        transition: 0.3s opacity;
        &:hover{
          cursor: pointer;
          opacity: .6;
        }
      }
    }
  }
  &-item{
    display: flex;
    justify-content: center;
    flex-direction: column;
    &__content{
      background: #FFFFFF;
      box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05);
      border-radius: 10px;

      padding: 15px 20px;
    }
    &__header{
      display: flex;
      align-items: center;
    }
    &__name{
      font-family: 'Futura PT',sans-serif;
      font-weight: 500;
      font-size: 24px;
    }
    &__social{
      margin-left: 20px;

      display: flex;
      align-items: center;
      &-link + &-link{
        margin-left: 15px;
      }
      &-link{
        font-size: 16px;
        color:rgba(1, 153, 52, 1);

        text-decoration: underline;
        text-underline-position: under;
        &_insta{
          color:rgba(105, 72, 231, 1);
        }
      }
    }
    &__footer{
      margin-top: 10px;
    }
    &__desc{
      color:rgba(40, 40, 40, .5);
      font-size: 16px;
    }
    &__img{
      width: 100%;
      max-width: 505px;
      max-height: 586px;
      height: 100%;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}

.hero{
  &-slider{
    &__controls{
      position: absolute;
      right: 25px;
      bottom: 60px;
      display: flex;
      align-items: center;
      z-index: 2;
      .swiper-pagination{
        position: relative;
        bottom: unset;
        left: unset;
        &-bullet{
          background-color: #E5E5E5;
          width: 10px;
          height: 10px;
          opacity: 1;
          transition: .3s opacity;
          &:hover{
            opacity: .6;
          }
          &-active{
            background: linear-gradient(131.82deg, #5D44DE 0%, #8952FF 96.22%);
          }
        }
      }
    }
  }
}